<template>
    <nav>
        <v-snackbar v-model="snackbar" :timeout="4000" top color="success">
            <span>Congratulations! Your study has been posted!</span>
            <v-btn flat color="white" @click="snackbar=false">Close</v-btn>
        </v-snackbar>

        <v-toolbar flat app>
            <v-toolbar-side-icon class="primary--text" @click="drawer = !drawer"></v-toolbar-side-icon>
            <v-toolbar-title class="text-uppercase primary--text">
                <span class="font-weight-light">Recruit</span>
                <span>Participants</span>
            </v-toolbar-title>
            <v-spacer></v-spacer>

            <v-menu offset-y>
                <v-btn flat slot="activator" color="primary">
                    <v-icon left>expand_more</v-icon>
                    <span>Menu</span>
                </v-btn>
                <v-list>
                    <v-list-tile v-for="link in links" :key="link.text" router :to="link.route">
                        <v-list-tile-title>{{ link.text }}</v-list-tile-title>
                    </v-list-tile>
                </v-list>
            </v-menu>

            <v-btn v-if="!isAuthenticated" flat color="primary" to="/signin">
                <span>Sign in</span>
                <v-icon right>person_add</v-icon>
            </v-btn>
            <v-btn v-else flat color="primary" @click="logout">
                <span>Sign Out</span>
                <v-icon right>exit_to_app</v-icon>
            </v-btn>
        </v-toolbar>

        <v-navigation-drawer v-model="drawer" app temporary class="primary">
            <v-layout column align-center>
                <v-flex class="mt-5">
                    <v-avatar size="100">
                        <img :src="getProfilePhotoUrl()">
                    </v-avatar>

                    <p class="white--text subheading mt-1 text-xs-center">
                        {{getProfileName()}}
                    </p>
                </v-flex>
                <v-flex class="mt-4 mb-3">
                    <Popup @studyAdded="snackbar = true" />
                </v-flex>
            </v-layout>
            <v-list>
                <v-list-tile v-for="link in links" :key="link.text" router :to="link.route">
                    <v-list-tile-action>
                        <v-icon class="white--text">{{ link.icon }}</v-icon>
                    </v-list-tile-action>
                    <v-list-tile-content>
                        <v-list-tile-title class="white--text">{{ link.text }}</v-list-tile-title>
                    </v-list-tile-content>
                </v-list-tile>
            </v-list>
        </v-navigation-drawer>
    </nav>
</template>

<script>
import Popup from './Popup'
import firebase from 'firebase'
export default {
    components: { Popup },
    data(){
        return{
            drawer: false,
            links: [
                { icon: 'home', text: 'Home', route: '/'},
                { icon: 'dashboard', text: 'Studies', route: '/studies' },
                { icon: 'folder', text: 'My Posts', route: '/myposts' },
                { icon: 'group', text: 'Groups', route: '/groups' },
            ],
            snackbar: false         
        }
    },
    computed: {
        isAuthenticated() {
            return this.$store.getters.isAuthenticated;
        },

    },
    methods: {
        logout() {
            this.$store.dispatch('userSignOut');
        },
        getProfilePhotoUrl() {
            var profileSrc = "";
            if(this.isAuthenticated){
                var user = firebase.auth().currentUser;
                if (user != null) {
                    if (user.photoURL != "" && user.photoURL != null) {
                        profileSrc = user.photoURL;
                    } else {
                        profileSrc = "profile_default.png";
                    }
                } else {
                    profileSrc =  "profile_default.png";
                }
            } else {
                profileSrc = "profile_default.png";
            }
            return profileSrc;
        },
        getProfileName() {
            var displayName = "";
            if(this.isAuthenticated){
                var user = firebase.auth().currentUser;
                if(user != null){
                    displayName = user.displayName;
                } else {
                    displayName = "Visitor";
                }
            } else {
                displayName = "Guest";
            }
            return displayName;
        }                        
    }

}</script>
